.app {
  display: flex;
  flex-direction: row;
  min-height: 100vh;

  .info {
    flex: 1;
    display: flex;
    flex-direction: column;
    font-size: 20px;
    padding: 40px;
    max-width: 100vw;
    z-index: 1;

    .box {
      display: inline-block;
      background-color: #ffc90e;
      height: 40px;
      width: 40px;
    }

    .parts {
      display: flex;
      flex-direction: column;
      flex-grow: 1;

      .part {
        display: block;
        margin-top: 20px;
        align-items: center;

        &.title {
          display: flex;
          column-gap: 20px;
          margin-top: 0px;
        }

        &.italic {
          font-style: italic;
        }

        &.links {
          flex: 1;
          align-content: flex-end;
          align-items: flex-end;
          display: flex;
          margin-top: 40px;
          column-gap: 20px;

          >a {
            &:hover {
              opacity: 0.75;
            }

            >img {
              height: 40px;
              width: 40px;
            }
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 600px) {
  .app {
    .info {
      padding: 20px;
    }
  }
}